.container {
}
.header {
  text-align: center;
}
.list {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;

  width: 100vw;
  height: 100px;
  margin: 0px;
  background-color: black;
}

.link {
  text-decoration: none;
  color: wheat;
}

form {
  box-shadow: 0 0 5px 2px rgb(39, 38, 38);
  padding: 20px;
  width: 60%;
  margin: 2rem auto;
  border-radius: 10px;
}
.user-input,button {
  margin: 1rem;
  display: flex;
  justify-content: space-around;
  align-items: center;
  color: rgb(29, 27, 27);
}
input,label,button{
    width:200px;
    height:30px;
    box-shadow: 0 0 2px 2px rgb(196, 184, 184);
    border: none;
    
    
}
label{
    box-shadow: none;
}

button{
    margin:0px auto;
    width:200px;
}
button:hover{
    color:white;
    background-color: black;
}

.profile-page{
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap:1rem;
  /* width:60vw;
  height:500px; */

}

.profile-page img{
  width:200px;
  height:200px;
  
}
.profile-page main{
  background:black;
  color: wheat;
  padding:2rem;
  box-shadow: 15px 15px 10px 2px rgb(122, 121, 121);
  border-radius: 12px;
}

.order-list{
  color:wheat;
}